[data-widget=rainbow_discord_widget] {
    .rainbow {
        &-content {
            background: var(--color-contrast-lower);
            padding: calc(#{$grid-gutter-width} / 2);
        }

        &-usersDiscord {
            height: 200px;
            overflow: auto;

            &::-webkit-scrollbar {
                width: 7px;
                border-radius: 50px;
            }

            &::-webkit-scrollbar-track {
                background-color: rgba(0, 0, 0, 0.149);
                border-radius: 50px;
            }

            &::-webkit-scrollbar-thumb {
                background-color: var(--color-primary);
                border-radius: 50px;
            }
        }

        &-discordUser {
            display: flex;
            align-items: center;
            padding: 5px;
        }

        &-userImg {
            background: rgba(0, 0, 0, .149) no-repeat center/cover;
            height: 20px;
            width: 20px;
            display: inline-block;
            border-radius: 5px;
            margin-right: 10px;
            position: relative;
        }

        &-username {
            font-weight: 600;
            font-size: 13px;
            opacity: .8;
        }

        &-userStatus {
            border-radius: 50%;
            display: block;
            position: absolute;
            width: 8px;
            height: 8px;
            left: auto;
            top: -5px;
            right: -5px;
        }

        &-status-dnd {
            background: var(--color-error);
        }

        &-status-idle {
            background: var(--color-warning);
        }

        &-status-online {
            background: var(--color-success);
        }

        &-discordInfo {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 16px;
        }

        &-js--discordCount {
            text-align: center;
            font-weight: $font-weight-bold;
            font-size: 13px;
            text-transform: uppercase;
            color: var(--color-primary);
        }

    }

    .btn-joinDiscord {
        padding: 3px 10px;
        font-size: 13px;
        background-color: var(--color-primary);
    }
}
